.dropdown {
    position: relative;
    z-index: 1;
    
}

.menu {
    /* position: absolute; */
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    border: 1px solid grey;
    width: 150px;
}

.menu>li {
    margin: 0;

    background-color: white;
}

.menu>li:hover {
    background-color: lightgray;
}

.menu>li>button {
    width: 100%;
    height: 100%;
    text-align: left;

    background: none;
    color: blue;
    border: none;
    padding: 5px;
    margin: 0;
    font: inherit;
    cursor: pointer;
}
.dropdown>button{
    font: inherit;
    color: blue;
    width: 150px;
}